import img from "../../assets/images/car-1.png";

const TotalCars = ({cars}) => {
  return ( 
    <div className="total-cars">
      <h4 className="total-cars__title">Всего {cars.length} автомобилей</h4>
      <div className="total-cars__cards">
        <div className="total-cars__card">
          <img className="total-cars__img" src={img} alt="car" />
        </div>
        <div className="total-cars__card">
          <img className="total-cars__img" src={img} alt="car" />
        </div>
        <div className="total-cars__card">
          <img className="total-cars__img" src={img} alt="car" />
        </div>
        <div className="total-cars__card">
          <img className="total-cars__img" src={img} alt="car" />
        </div>
      </div>
    </div>
   );
}
 
export default TotalCars;