import './faq.scss';

import { useState } from 'react';
import { Data } from './Data';

const Faq = () => {
  const [clicked, setClicked] = useState(false);

  const toggle = index => {
    if (clicked === index ) {
      return setClicked(null);
    }
    setClicked(index)
  }

  return ( 
    <div className="faq">
      {Data.map((item, index) => {
        return (
          <div className="faq__item" key={index}>
          <button className="faq__title" onClick={() => toggle(index)}>
            <span>{clicked === index ? '-' : '+'}</span>
            {item.q}
          </button>
          { clicked === index ? (
            <p className="faq__text">{item.a}</p>
          ) : null }
        </div>
        )
      })}
    </div>
   );
}
 
export default Faq;