const TicketRate = () => {
  return ( 
    <div className="ticket-rate">
      <div className="ticket-rate__card">
        <div className="ticket-rate__title-wrap">
          <h3 className="ticket-rate__title">Economy</h3>
          <span className="ticket-rate__price">1990 ₸</span>
        </div>
        <p className="ticket-rate__description">
          (дешевле билет - больше участников)
        </p>
      </div>
      <div className="ticket-rate__card ticket-rate__card_active">
        <div className="ticket-rate__title-wrap">
          <h3 className="ticket-rate__title">Classic</h3>
          <span className="ticket-rate__price">4990 ₸</span>
        </div>
        <p className="ticket-rate__description">
          (дороже билет - меньше участников)
        </p>
      </div>
      <div className="ticket-rate__card">
        <div className="ticket-rate__title-wrap">
          <h3 className="ticket-rate__title">Express</h3>
          <span className="ticket-rate__price">9990 ₸</span>
        </div>
        <p className="ticket-rate__description">
          (дороже билет - высокие шансы)
        </p>
      </div>
    </div>
   );
}
 
export default TicketRate;