import img from "../../assets/images/car-1.png";
const Cars = ({cars}) => {
  const limit_cars = cars.slice(0,4);
  return ( 
    <div className="cars">
      {limit_cars.map((car) => (
        <a href={car.link} className="car" key={ car.id }>
          <img className="car__img" src={img} alt="car" />
          <div className="car__body">
            <h6 className="car__title">{ car.title }, { car.year } г.</h6>
            <span className="car__tech">{ car.tech.engine_volume }, { car.tech.transmission }, { car.tech.mileage }</span>
            <div className="car__info">
              <div className="car__info-item">
                <span className="car__info-item-text">Продано билетов</span>
                <span className="car__info-item-text car__info-item-text_bold">{ car.tickets.sold } из { car.tickets.total }</span>
              </div>
              <div className="car__info-item">
                <span className="car__info-item-text">Дата розыгрыша</span>
                <span className="car__info-item-text car__info-item-text_bold">{ car.date }</span>
              </div>
            </div>
          </div>
        </a>
      ))}
    </div>
   );
}
 
export default Cars;