import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss';

import './home.scss'

import icon_link_blue_1x from "../../assets/images/icon-arrow-blue-1x.png";
import icon_link_blue_2x from "../../assets/images/icon-arrow-blue-2x.png";
import icon_link_accent_1x from "../../assets/images/icon-arrow-accent-1x.png";
import icon_link_accent_2x from "../../assets/images/icon-arrow-accent-2x.png";
import home_banner_1x from "../../assets/images/home-banner-1-1x.jpg";
import home_banner_2x from "../../assets/images/home-banner-1-2x.jpg";
import home_img_1_1x from "../../assets/images/home-img-1-1x.jpg";
import home_img_1_2x from "../../assets/images/home-img-1-2x.jpg";

import Cars from './Cars';
import useFetch from '../useFetch';
import TicketRate from './TicketRate';
import TotalCars from './TotalCars';
import Winners from './Winners';
import CheckTicket from './CheckTicket';
import AddCarBanner from './AddCarBanner';
import GameInfo from './GameInfo';
import News from '../News/News';
import Faq from '../Faq/Faq';

const Home = () => {
  const { data: cars, isPending, error } = useFetch(`https://simple-b.herokuapp.com/cars`);
  const { data: winners, isPending_winners, error_winners } = useFetch(`https://simple-b.herokuapp.com/last_winners`);
  const { data: news, isPending_news, error_news } = useFetch(`https://simple-b.herokuapp.com/news`);

  return ( 
    <main className="main-content">
      <section className="ticket-rate-wrap">
        <div className="container">
          <div className="row">
            <TicketRate />
          </div>
        </div>
      </section>
      <section className="total-cars-wrap">
        <div className="container">
          <div className="row">
            { isPending && <h3>Имитация загрузки...</h3> }
            { error && <h3>{error}</h3> }
            { cars && <TotalCars cars={cars} /> }
            { 
              cars &&
                <a className="total-cars__link" href="/">
                  <img src={icon_link_blue_1x} srcSet={icon_link_blue_2x + ' 2x'} alt="icon" />
                  Посмотреть все авто
                </a>
              }
          </div>
        </div>
      </section>
      <section className="home-game-info">
        <div className="container">
          <div className="row">
            <div className="home-game-info__content">
              <GameInfo />
              <div className="home-game-info__img">
                <img src={home_img_1_1x} srcSet={home_img_1_2x + ' 2x'} alt="img" />
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="soon-games">
        <div className="container">
          <div className="row">
            <h2 className="section-title">Ближайшие розыгрыши</h2>
            { isPending && <h3>Имитация загрузки...</h3> }
            { error && <h3>{error}</h3> }
            { cars && <Cars cars={cars} /> }
          </div>
        </div>
      </section>
      <section className="winners-wrap">
        <div className="container">
          <div className="row">
            <h2 className="section-title">Победители</h2>
            { winners && <Winners winners={ winners } /> }
          </div>
        </div>
      </section>
      <section className="home-banner">
        <div className="container">
          <div className="row">
          <img className="home-banner__img" src={home_banner_1x} srcSet={home_banner_2x + ' 2x'}   alt="home-banner" />
          </div>
        </div>
      </section>
      <section className="check-ticket background-centered">
        <div className="container">
          <div className="row">
              <div className="check-ticket__content">
              <h3 className="check-ticket__title">
                Проверить билет на выигрыш
              </h3>
              <CheckTicket />
              </div>
          </div>
        </div>
      </section>
      <section className="home-banner-add-car background-centered">
        <div className="container">
          <div className="row">
            <AddCarBanner />
          </div>
        </div>
      </section>
      <section className="news-wrap">
        <div className="container">
          <div className="row">
            <h2 className="section-title">Новости</h2>
            { news && <News news={news} /> }
          </div>
        </div>
      </section>
      <section className="home-banner-3 background-centered">
        <div className="container">
          <div className="row">
            <div className="home-banner-3__content">
              <div className="home-banner-3__col">
                <h3 className="home-banner-3__title">Хотите посетить розыгрыш?</h3>
                <a href="/" className="home-banner-3__link">
                  <img src={icon_link_accent_1x} srcSet={icon_link_accent_2x + ' 2x'} alt="icon" />
                  Узнать подробнее
                </a>
              </div>
              <div className="home-banner-3__col">
                <a href="/" className="home-banner-3__button main-button">Оставить заявку</a>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="faq-wrap">
        <div className="container">
          <div className="row">
            <Faq />
          </div>
        </div>
      </section>
    </main>
   );
}
 
export default Home;