import winners_icon_1x from "../../assets/images/winners-icon-1x.png";
import winners_icon_2x from "../../assets/images/winners-icon-2x.png";
import winners_img_1x from "../../assets/images/winners-img-1x.jpg";
import winners_img_2x from "../../assets/images/winners-img-2x.jpg";


const Winners = ({winners}) => {
  const last_winners = winners.slice(-4);
  return (
    <div className="winners">
      {last_winners.map((winner) => (
        <div className="winners__card" key={winner.id}>
        <img src={winners_img_1x} srcSet={winners_img_2x + ' 2x'} alt="pho" className="winners__img" />
        <img src={winners_icon_1x} srcSet={winners_icon_2x + ' 2x'} alt="icon" className="winners__icon" />
        <span className="winners__car">{winner.car.name}, {winner.car.year} г.</span>
        <h3 className="winners__title">{winner.name}</h3>
        <span className="winners__info">
          {winner.age} года, {winner.city}
        </span>
      </div>
      ))}
    </div>
   );
}
 
export default Winners;