import './header.scss';
import logo_1x from "../../assets/images/main-logo-1x.png";
import logo_2x from "../../assets/images/main-logo-2x.png";
import sj_logo_1x from "../../assets/images/sj-logo-1x.png";
import sj_logo_2x from "../../assets/images/sj-logo-2x.png";

const Header = () => {
  return ( 
    <header className="main-header">
      <div className="container">
        <div className="row">
          <div className="main-header__content">
          <div className="burger-menu">
        <div className="burger-menu__icon">
          <div className="burger-menu__icon_line"></div>
          <div className="burger-menu__icon_line"></div>
          <div className="burger-menu__icon_line"></div>
        </div>
        <div className="burger-menu__content"></div>
      </div>
      <a href="/" className="main-header__logo">
        <img src={logo_1x} srcSet={logo_2x + ' 2x'} alt="logo" />
      </a>
      <p className="main-header__text">
        Официальная автолотерея
      </p>
      <a href="/" className="main-header__logo">
        <img src={sj_logo_1x} srcSet={sj_logo_2x + ' 2x'} alt="logo  " />
      </a>
      <div className="main-header__buttons">
        <div className="main-header__langs">
          <div className="main-header__lang">KZ</div>
          <div className="main-header__lang main-header__lang_active">
            RU
          </div>
        </div>
        <a href="/" className="main-button main-header__button main-header__button_outline">Правила участия</a>
        <a href="/" className="main-button main-header__button">Вход | Регистрация</a>
      </div>
          </div>
        </div>
      </div>
    </header>
   );
}
 
export default Header;