import img from "../../assets/images/car-1.png";

import './news.scss';

const News = ({news}) => {
  console.log(news)
  const last_news = news.slice(-4);
  return ( 
    <div className="news">
      {last_news.map((new_item, i) => (
        <a href="/" className="news__card" key={i}>
          <span className="news__date">{new_item.date}</span>
          <img src={img} alt="img" className="news__img" />
          <h4 className="news__title">{new_item.title}</h4>
          <div className="news__content">
            <h4 className="news__title">{new_item.title}</h4>
            <p className="news__subtitle">{new_item.subtitle}</p>
          </div>
        </a>
      ))}
    </div>
   );
}
 
export default News;