import './footer.scss';

import footer_img_1_1x from '../../assets/images/footer/footer-img-1-1x.png';
import footer_img_1_2x from '../../assets/images/footer/footer-img-1-2x.png';
import footer_logo_1x from '../../assets/images/footer/footer-logo-1x.png';
import footer_logo_2x from '../../assets/images/footer/footer-logo-2x.png';
import footer_icon_1_1x from '../../assets/images/footer/footer-icon-1-1x.png';
import footer_icon_1_2x from '../../assets/images/footer/footer-icon-1-2x.png';
import footer_icon_2_1x from '../../assets/images/footer/footer-icon-2-1x.png';
import footer_icon_2_2x from '../../assets/images/footer/footer-icon-2-2x.png';

const Footer = () => {
  return ( 
    <footer className="main-footer">
      <div className="container">
        <div className="row">
          <div className="main-footer__img-wrap">
            <img className="main-footer__img" src={footer_img_1_1x} srcSet={footer_img_1_2x + ' 2x'} alt="img" />
          </div>
        </div>
        <div className="row">
          <div className="main-footer__content">
            <div className="main-footer__col">
              <img src={footer_logo_1x} srcSet={footer_logo_2x + ' 2x'} alt="logo" className="main-footer__logo" />
              <a href="/" className="main-footer__link">© 2021 AvtoLoto</a>
            </div>
            <div className="main-footer__col">
              <h6 className="main-footer__title">Главное</h6>
              <ul className="main-footer__link-list">
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Проверка билетов</a>
                </li>
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Получить выигрыш</a>
                </li>
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Победители</a>
                </li>
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Правила участия в лотереи</a>
                </li>
              </ul>
            </div>
            <div className="main-footer__col">
            <h6 className="main-footer__title">Avto Loto</h6>
            <ul className="main-footer__link-list">
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Проверка билетов</a>
                </li>
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Получить выигрыш</a>
                </li>
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Победители</a>
                </li>
                <li className="main-footer__link-item">
                  <a href="/" className="main-footer__link">Правила участия в лотереи</a>
                </li>
              </ul>
            </div>
            <div className="main-footer__col">
              <h6 className="main-footer__title">Поддержка</h6>
              <a href="tel:123456789" className="main-footer__tel">
                <img src={footer_icon_1_1x} srcSet={footer_icon_1_2x + ' 2x'} alt="phone" />
                8 800 080 5200
              </a>
              <p className="main-footer__link">
                Республика Казахстан, город Алматы, A25D7D4, проспект Достык 240
              </p>
            </div>
            <div className="main-footer__col">
              <h6 className="main-footer__title">Мы в соцсетях</h6>
              <img src={footer_icon_2_1x} srcSet={footer_icon_2_2x + ' 2x'} alt="social" />
            </div>
          </div>
        </div>
      </div>
    </footer>
   );
}
 
export default Footer;