const CheckTicket = () => {
  return ( 
    <form action="#" method="GET" className="check-ticket__form">
      <div className="check-ticket__input-wrap">
        <label htmlFor="game-id" className="check-ticket__label">
          Номер розыгрыша
        </label>
        <input maxLength="10" type="number" className="check-ticket__input" id="game-id" />
      </div>
      <div className="check-ticket__input-wrap">
        <label htmlFor="ticket-id" className="check-ticket__label">
          Номер билета
        </label>
        <input maxLength="10" type="number" className="check-ticket__input" id="ticket-id" />
      </div>
      <button type="submit" className="main-button check-ticket__button">Проверить</button>
    </form>
   );
}
 
export default CheckTicket;